.mainContainer {
  flex-direction: column;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100vw
}

.titleContainer {
  display: flex;
  flex-direction: column;
  font-size: 64px;
  font-weight: bolder;
  align-items: center;
  justify-content: center;
  position: relative;
}

.titleContainer .window_close{
  width: 2rem;
  height: 2rem;
  font-size: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  right: -8rem;
  top: -2rem;
  border-radius: 5px;
  border: 2px solid black;
  background-color: rgb(239, 130, 130);
  cursor: pointer;
}

.resultContainer,
.historyItem {
  flex-direction: row;
  display: flex;
  width: 400px;
  align-items: center;
  justify-content: space-between;
}

.historyContainer {
  flex-direction: column;
  display: flex;
  height: 200px;
  align-items: center;
  flex-grow: 5;
  justify-content: flex-start;
}

.buttonContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 260px;
}

.inputContainer {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  margin: 1rem;
}

.inputContainer > .errorLabel {
  color: red;
  font-size: 12px;
}

.inputContainer .inputBox {
  height: 48px;
  width: 400px;
  font-size: large;
  border-radius: 8px;
  border: 1px solid grey;
  padding-left: 8px;

}

.inputlogin {
  display: flex;
  flex-direction: row;
  height: 48px;
  width: 400px;
  justify-content: space-around;
}

.inputlogin .inputButton{
  font-size: large;
  border-radius: 8px;
  border: 1px solid grey;
  padding-inline: 8px;
  cursor: pointer;
}

.inputlogin .inputButton:active{
  transform: translateY(4px);
}
